import React, { useEffet } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';
import { getDetailCountry } from '../../Redux/actions.js';

const DetailCountries = () => {

	const detailCountry = useSelector((state) => state.countriesDetail);
	const dispatch = useDispatch();

	let { id } = useParams();

	useEffet(() => {
		dispatch(getDetailCountry(id))
	}, [id])

	return (
		<div>

		<div>

		<h1>Country details: </h1>
		<h2>{detailCountry.name - detailCountry.id}</h2>
		<h3>Continent: {detailCountry.continent}</h3>
		<h5>Capital: {detailCountry.capital}</h5>
		<h5>Subregion: {detailCountry.subregion}</h5>
		<h5>Area: {detailCountry.area} km^2</h5>
		<h5>Population: {detailCountry.population}</h5>

		</div>

		<div>
		  <img src={detailCountry.flag_image} alt="bandera" />
		</div>







		</div>

		)




};


export default DetailCountries;